
.price-calculator {
    padding: 20px;
    align-items: center;
    max-width: 400px;
    width: 100%;
    border: 3px solid #ddd;
    border-radius: 15px;
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(9, 224, 38, 0.329);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0 auto; /* Center the container horizontally */
    direction: rtl;
}  
  .price-calculator h2 {
    font-size: 20px;
    margin-bottom: 16px;
    text-align: center; /* Centered heading */
  }
  
  .price-calculator label {
    display: block;
    margin: 8px 0;
    font-size: 16px;
  }
  
  .price-calculator input {
    width: 100%;
    padding: 8px;
    margin-top: 4px;
    margin-bottom: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box; /* Ensures padding doesn’t affect width */
  }
  
  .price-calculator h3 {
    margin-top: 20px;
    font-size: 18px;
    color: #333;
    text-align: center; /* Centered total price */
  }
  
  /* Responsive adjustments for smaller screens */
  @media (max-width: 600px) {
    .price-calculator {
      max-width: 90%; /* Take up 90% of the screen width */
    }
  
    .price-calculator h2 {
      font-size: 18px; /* Smaller heading */
    }
  
    .price-calculator h3 {
      font-size: 16px; /* Smaller total price */
    }
  }
  