/* Global Reset */
html, body {
  margin: 0; /* Remove default margin */
  padding: 0; /* Remove default padding */
  padding-top: 60px; 
  height: 100%; /* Ensure full height for body */
}

/* .content {
  top: 160px;
  position: absolute;
} */

.hero {
  background-color: #ffe5b4;
  border-radius: 8px;
  text-align: center;
 
}
.price-calculator-toggle {
  padding: 10px 20px; /* Button padding */
  background-color: #19d156d3; /* Green background */
  color: white; /* Text color */
  border: none; /* Remove default border */
  border-radius: 15px; /* Rounded corners */
  cursor: pointer; /* Pointer on hover */
  font-size: 16px; /* Font size */
  transition: background-color 0.3s ease; /* Smooth transition for background color */
  margin-top: 20px; /* Optional margin */
  margin-bottom: 15px;
}

.price-calculator-toggle:hover {
  background-color: #45a049; /* Darker green on hover */
}

.price-calculator-toggle:focus {
  outline: none; /* Removes the default focus outline */
}

.description p {
  font-family: "Secular One", sans-serif; /* Font style */
  font-size: 1.5em; /* Font size */
  line-height: 1.5; /* Line height */
  margin: 0 0 15px 0; /* Margin below paragraphs */
}
.hero-image {
  width: 100%;
  max-width: 600px;
  border-radius: 8px;
}

#menu {
  margin-top: 20px;
}

.menu-item {
  display: inline-block;
  width: 250px;
  margin: 10px;
  text-align: center;
}

.pizza-image {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.menu-item h4 {
  margin-top: 10px;
  font-size: 1.2em;
}
.call-button{
  background-color: #19d156d3;
  border-radius: 15px;
  color: rgb(255, 255, 255);
  direction: rtl;
}
.menu-item p {
  margin: 5px 0;
}
/* src/App.css */

