/* src/components/Info.css */

/* Container styling */
.info-container {
    padding: 20px;
    text-align: right;
    background-color: #f9f9f9;
    border: 1px solid #ccc;
    border-radius: 5px;
    max-width: 800px;
    margin: auto; /* Center on the page */
    margin-top: 40px;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  /* Title styling */
  .info-container h1 {
    font-size: 1.8rem;
    color: #333;
    margin-bottom: 10px;
  }
  
  /* Paragraph styling */
  .info-container p {
    font-size: 1rem;
    color: #555;
    line-height: 1.6;
    margin-bottom: 15px;
  }
  
  /* Responsive styling */
  @media (max-width: 600px) {
    .info-container {
      padding: 15px;
      text-align: center;
    }
  
    .info-container h1 {
      font-size: 1.5rem;
    }
  
    .info-container p {
      font-size: 0.9rem;
    }
  }
  