/* src/components/ContactUs.css */
.contact-us-container {
  max-width: 800px;
  padding: 60px;
  text-align: center;
  margin:auto ;
}

.contact-us-container h1 {
  margin-bottom: 10px;
  margin-top: 1px;
  font-size: 2em;
}

.contact-us-container p {
  margin-bottom: 20px;
  font-size: 1.2em;
}

/* Make the form and text responsive */
@media (max-width: 768px) {
  .contact-us-container {
    padding: 15px;
    margin-top: 40px;
  }
  .contact-us-container h1 {
    font-size: 1.8em;
  }
  .contact-us-container p {
    font-size: 1em;
  }
}

@media (max-width: 480px) {
  .contact-us-container h1 {
    font-size: 1.6em;
    margin-top: 30px;
  }
  .contact-us-container p {
    font-size: 0.9em;
  }
}
